import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';

import { Navigation, Pagination } from 'swiper/modules';
import { useRef } from 'react';

const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    prevArrow: <CustomPrevArrow />,
      nextArrow: <CustomNextArrow />,
  };
  
  function CustomPrevArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{
          ...style,
          display: "block",
          background: "green",
          borderRadius: "50%",
        }}
        onClick={onClick}
      >
        
      </div>
    );
  }
  
  function CustomNextArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{
          ...style,
          display: "block",
          background: "green",
          borderRadius: "50%",
        }}
        onClick={onClick}
      >
        
      </div>
    );
  }
  const images = [
    {src: '/bg9.jpg', title: 'Ton cnh d n'},
    {src: '/bg1.jpg', title: 'Trung tm Hi ch Trin lm Quc gia - The Grand Expo'},
    {src: '/bg2.jpg', title: 'Tng th d n'},
    {src: '/bg3.jpg', title: 'Vincom Mega Mall'},
    {src: '/bg4.jpg', title: 'Cng vin Royal Park'},
    {src: '/bg5.jpg', title: 'Nh  xe'},
    {src: '/bg6.jpg', title: 'Wonderland'},
    {src: '/bg7.jpg', title: 'Wonderland'},
    {src: '/bg8.jpg', title: 'Qung trng Thp ng h Boutique Park'},
  
  ]
  
function SwiperWrap(){
    const swiperRef = useRef(null);
    return <>
    <Swiper
      ref={swiperRef} 
        modules={[Navigation, Pagination]}
        slidesPerView={1.5}        // Hin th 1.5 nh trn mn hnh
        spaceBetween={10}          // Khong cch gia cc nh
        navigation                 // Thm nt chuyn
        pagination={{ clickable: true }} // Thm pagination
        loop={true}                // Cho php loop khi ht nh
        breakpoints={{
          768: {
            slidesPerView: 1.5, // Hin th 1.5 nh trn mn hnh t 768px tr ln (my tnh)
          },
          0: {
            slidesPerView: 1, // Hin th 1 nh trn mn hnh nh hn 768px (in thoi)
          },
        }}
      >
        {images.map((img, index) => (
          <SwiperSlide key={index}>
            <div style={{position: 'relative',height: ' 400px',overflow: 'hidden'}}>
            <img
              src={img.src}
              alt={`Slide ${index + 1}`}
              style={{
                width: '100%',
                boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)', // Thm shadow
                width: '100%',
                  height: '100%',      // t chiu cao 100% cho nh
                  objectFit: 'cover',  // Ct nh  gi t l
              }}
            />
            <div className='bg-overlay'>
                <h1 className='bg-title'>
                  {img.title} {/* Tiu  */}
                </h1>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
      
      {/* Nt Prev */}
      <div className="swiper-button-prev custom-nav-button" onClick={() => swiperRef.current.swiper.slidePrev()}></div>

      {/* Nt Next */}
      <div className="swiper-button-next custom-nav-button" onClick={() => swiperRef.current.swiper.slideNext()}></div>
    </>
}
  export default SwiperWrap