function App() {
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const closeModal = () => {
    setIsModalVisible(false);
  };
  const handleButtonClick = (record) => {
    showModal()
  };
  const columns = [
    {
      title: 'M cn',
      dataIndex: 'macan',
      key: 'macan',
      render: (text, record, index) => (
        <div className="table-row">
          {/* Render nhn "TOP" cho 3 hng u tin */}
          {index < 3 && (
            <span className="top-label hidden-column">
              {`TOP ${index + 1}`}
            </span>
          )}
          {text}
        </div>
      ),
    },
    {
      title: 'DT t',
      dataIndex: 'dientich',
      key: 'dientich',
    },
    {
      title: 'Gi bn',
      dataIndex: 'gia',
      key: 'gia',
    },
    {
      title: 'Gi/m2',
      dataIndex: 'giam2',
      key: 'giam2',
      className: 'hidden-column',
    },
    {
      title: 'Hng ca',
      dataIndex: 'huongcua',
      key: 'huongcua',
      className: 'hidden-column',
    },
    {
      title: 'c im',
      dataIndex: 'dacdiem',
      key: 'dacdiem',
      
    },
    {
      title: '',
      key: '',
      render: (text, record) => (
        <div className='lien-he-btn' onClick={() => handleButtonClick(record)}>
          Lin h
        </div>
      ),
      className: 'hidden-column',
    },
  
  ];
  
  return (
    <div className="App">
      <Header></Header>  
      <h2 className='tong-quan-title'>Tng quan d n Vinhomes Global Gate
      </h2>
      <div className='swiper-wrap'>
      <SwiperWrap></SwiperWrap>
      </div>
      <div className='table-wrap'>
        <h2 className='table-title'>
        Bng gi Vinhomes Global Gate thng 11/2024
        </h2>
      <Table 
      rowHoverable={false} 
      dataSource={dataSource} 
      pagination={false} columns={columns}/>
      </div>
      
      <div className='see-all' onClick={showModal}>Xem ton b qu cn <RightOutlined className="small-icon"/></div>
      <PhoneModal visible={isModalVisible} onClose={closeModal}></PhoneModal> 
    </div>
  );
}

export default App;
